import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
// import CardActions from '@material-ui/core/CardActions';
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
// import Button from '@material-ui/core/Button';
import Typography from "@material-ui/core/Typography";
import { useHistory } from "react-router-dom";

import StuffVid from "./assets/webm/stuff.webm";

import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1
  },
  root2: {
    maxWidth: 345
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary
  }
}));

const stuff = [
  { title: "BattleStation", alt: "battlestation", path: "/gear/battlestation", imgurl: "https://i.imgur.com/2ar80ao.jpg", description: "Where the magic happens" },
  { title: "Home Theatre", alt: "hometheatre", path: "/gear/hometheatre", imgurl: "https://i.imgur.com/R9Wfcn7.jpg", description: "Where things are watched" },
  { title: "Bag", alt: "bag", path: "/gear/bag", imgurl: "https://i.imgur.com/I2lLgcr.png", description: "Where things are stored" }
];

export default function Gear() {
  const classes = useStyles();
  let history = useHistory();

  return (
    <div className={classes.root}>
      <Grid container direction="row" justify="flex-start" alignItems="flex-start" spacing={2}>
        {stuff.map(thing => {
          return (
            <Grid item sm={6} md={4} lg={2}>
              <Card
                className={classes.root2}
                onClick={() => {
                  history.push(thing.path);
                }}
              >
                <CardActionArea>
                  <CardMedia component="img" alt={thing.alt} height="140" image={thing.imgurl} title={thing.title} />
                  <CardContent>
                    <Typography gutterBottom variant="h5" component="h2">
                      {thing.title}
                    </Typography>
                    <Typography variant="body2" color="textSecondary" component="p">
                      {thing.description}
                    </Typography>
                  </CardContent>
                </CardActionArea>
              </Card>
            </Grid>
          );
        })}
        <Grid item sm={12} md={12} lg={6}>
          <Card>
            <CardContent>
              <video id="stuffvid" src={StuffVid} type="video/mp4" autoPlay muted style={{ width: "100%" }} />
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </div>
  );
}

